.popup_plan {
  position: relative;
  max-width: 1100px;
  margin: 5% auto 0 auto;
}
.img_wr {
  width: 931px;
  position: relative;
}
.popup_plan .title {
  font-weight: bold;
  font-size: 30px;
  text-align: left;
  text-transform: uppercase;
  padding-bottom: 20px;
}
.floor-img {
  display: block;
  object-fit: cover;
}
.svg-floor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.floors {
  fill: rgba(0, 255, 38, 0.5);
  transition: 0.3s ease;
}
.floor__apartment-2:hover,
.floor__apartment-2:focus {
  /*fill: rgba(127, 191, 128, 0.3);*/
  fill: rgba(255, 0, 0, 0.5);
}
.floor__apartment-3:hover,
.floor__apartment-3:focus {
  /*fill: rgba(254, 191, 148, 0.3);*/
  fill: rgba(255, 255, 0, 0.4);
}
.floor__apartment-4:hover,
.floor__apartment-4:focus {
  /*fill: rgba(242, 149, 132, 0.3);*/
  fill: rgba(0, 255, 38, 0.4);
}
.floor__apartment-5:hover,
.floor__apartment-5:focus {
  fill: rgba(142, 178, 240, 0.3);
}
.floor__apartment-1:hover,
.floor__apartment-1:focus {
  fill: rgba(127, 223, 222, 0.3);
}
.floor__apartment-7:hover,
.floor__apartment-7:focus {
  fill: rgba(191, 191, 255, 0.3);
}
.floor__apartment-6:hover,
.floor__apartment-6:focus {
  fill: rgba(248, 217, 152, 0.3);
}
.popup_plan .subtitle {
  margin: 0 0 20px 0;
  padding: 0;
  font-size: 24px;
}
.apartment__popup {
  display: none;
  position: absolute;
  top: 60px;
  right: 600px;
  width: 300px;
  padding: 20px;
  box-shadow: 0 5px 15px -5px #000000;
  background-color: #ffffff;
  transition: 0.3s ease;
  z-index: 500;
}
.apartment__popup--active {
  display: block;
}
.apartment__popup-text {
  margin: 0 0 20px 0;
}
